import InputError from "components/InputError";
import Label from "components/Label/Label";
import { withAuth } from "components/hoc/withAuth";
import { IBaseInputCommonProps } from "components/interface/Input";
import { RadioButton, RadioButtonProps } from "primereact/radiobutton";
import React from "react";

export interface IBaseRadioButtonProps extends RadioButtonProps, IBaseInputCommonProps {}
const BaseRadioButton: React.FC<IBaseRadioButtonProps> = (props: IBaseRadioButtonProps) => {
  const {
    hideLabel,
    label,
    labelClassNames,
    labelStyleObj,
    validation,
    hideErrorRow,
    errorClassNames,
    errorStyleObj,
    disabled,
    isDisabledByAcl,
    ...PrimeReactProps
  } = props;

  const { className, value, style, name, onChange } = PrimeReactProps;

  return (
    <div className="group grid">
      {!hideLabel && <Label label={label} labelClassNames={labelClassNames} />}

      <RadioButton
        // className={className}
        value={value}
        style={style}
        onChange={onChange}
        name={name}
        disabled={disabled || isDisabledByAcl}
        {...PrimeReactProps}
        className={`${className} ${
          isDisabledByAcl
            ? "!bg-secondary-disabledBGColor !border-primary-gray-300 placeholder-secondary-gray-500"
            : " "
        } `}
      />
      {!hideErrorRow && (
        <InputError
          validation={validation}
          hideErrorRow={hideErrorRow}
          errorClassNames={errorClassNames}
        />
      )}
    </div>
  );
};

export default withAuth(BaseRadioButton);
